import React, {useState} from 'react'
import "./Converter.css"

function Binary({id, binaries, setBinaries, setValue}) {
    const [binary, setBinary] = useState(false)

    const btnHandlre = e => {
        let newArr = [...binaries]
        setBinary(!binary)
        if (!binary) {
            newArr[id] = 1
            setBinaries(newArr)
            
        }
        else {
            newArr[id] = 0
            setBinaries(newArr)
        }
        console.log("working")
        setTimeout(function () {
            
        },1000)

    }
    const btnHandlre2 = e => {
        e.preventDefault()
        setValue(parseInt((binaries.join("").split("").reverse().join("")), 2))
        
    }

    return (
        <div className="btnbtn">
            <button className="btn" onMouseDown={btnHandlre} onMouseUp={btnHandlre2}>
            {binaries[id]}</button>
            
        </div>
    )
}

export default Binary
